
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Oswald:wght@200..700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Protest+Riot&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');

:root{
  --colorWhiteText:'#fff';
  --colorWhiteDesc:'gray';
  --colorDarkText:'#000';
  --colorDarkDesc:'gray'
}
*{
  margin: 0;
  padding: 0;
}
body{
  background-color: #000;
  font-family: 'Montserrat','sans-serif';
}

.dark{
  background-color: #000;
  color: var(--colorDarkText)
}
.light{
  background-color:white;
  color:var(--colorWhiteText)
}

